.container-howwedo {
    width: 100%;
    padding-left: 15px;
    padding-right: 15px;
    margin: 0 auto;
    margin-top: 50px;
    margin-bottom: 50px;
}
.section-howwedo__title-section {
    font-weight: 800;
    font-size: 20px;
    line-height: 1.3;
    text-align: center;
    letter-spacing: 0.03em;
    color: var(--main-title-color);
    margin-top: 0;
    margin-bottom: 30px;
}
.section-howwedo__list {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0;
    margin: 0;
    list-style: none;
}
.section-howwedo__item {
    width: 280px;
    height: 280px;
    padding-bottom: 22px;
    padding-top: 30px;
    background: var(--second-text-color);
    box-shadow: 0px 8px 26px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column;
    align-items: center;

    transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1),
    color 250ms cubic-bezier(0.4, 0, 0.2, 1),
    background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

    &:not(:last-child) {
        margin-bottom: 15px;
    }
    &:hover, &:focus {
    background-color: var(--accent-color);
    }
    &:hover .section-howwedo__icon,
    &:focus .section-howwedo__icon {
    fill: var(--second-text-color);
    }
    &:hover .section-howwedo__title, &:focus .section-howwedo__title {
    color: var(--second-text-color);
    }
    &:hover .section-howwedo__title::after, &:focus .section-howwedo__title::after {
    color:  var(--second-text-color);
    }
    &:hover .section-howwedo__text, &:focus .section-howwedo__text {
    color:  var(--second-text-color);
    }
}
.section-howwedo__icon {
    fill: var(--accent-color);
}
.section-howwedo__description {
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.section-howwedo__title {
    display: block;
    width: 249px;
    height: 39px;
    margin: 0 0 32px 0;
    text-align: center;
    font-weight: bold;
    font-size: 14px;
    line-height: 1.43;
    letter-spacing: 0.08em;
    color: #111518;
}
.section-howwedo__title::after {
    content:"-";
    display: block;
    font-weight: bold;
    font-size: 20px;
    line-height: 1.65;
    align-items: center;
    text-align: center;
    letter-spacing: 0.08em;
    color: var(--accent-color);

}
.section-howwedo__text {
    display: block;
    margin: 0;
    text-align: center;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.67;
    letter-spacing: 0.08em;
    color: var(--main-paragraph-color);
}
@media screen and (min-width: 768px) {
    .container-howwedo {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .section-howwedo__title-section {
        font-size: 26px;
    }
    .section-howwedo__list {
        flex-direction: row;
        flex-wrap: wrap;
        width: 688px;
        margin-top: -27px;
        margin-left: -20px;
    }
    .section-howwedo__item {
        margin-top: 27px;
        margin-left: 20px;
        flex-basis: calc(100% / 2 - 20px);
        height: 334px;
        padding-bottom: 30px;
        padding-top: 40px;
    }
    .section-howwedo__item:not(:last-child) {
        margin-bottom: 0;
    }
    .section-howwedo__description {
        margin-top: 30px;
        margin-right: 22px;
        margin-left: 22px;
    }
}

@media screen and (min-width: 1366px) {
    .section-howwedo__title-section {
        font-size: 46px;
        text-align: left;
        margin-bottom: 50px;
    }
    .section-howwedo__list {
        width: 1314px;
        margin-top: -20px;
    }
    .section-howwedo__item {
        margin-top: 20px;
        flex-basis: calc(100% / 3 - 20px);
        height: 396px;
        padding-bottom: 40px;
        padding-top: 55px;
    }
    .section-howwedo__description {
        margin-right: 30px;
        margin-left: 30px;
    }  
    .section-howwedo__title {
        font-size: 20px;
        line-height: 1.4;
        width: 365px;
        height: 53px;
    } 
    .section-howwedo__text {
        font-size: 18px;
        line-height: 1.56;
    }   
}

